body {
  background: linear-gradient(0deg, rgba(1, 5, 9, 1) 20%, rgba(1, 3, 8, 1) 96%);
  min-height: 100vh;
}

h1 {
  text-align: center;
  font-size: clamp(12px, 8vw, 80px);
}

#video {
  max-height: 50vh;
  opacity: 0.7;
  z-index: -10;
  position: absolute;
  top: 0;
  width: 100%;
}